*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Sofia",sans-serif;
    }
    section{
    position:relative;
    min-height: 100vh;
    background: #37a7fc;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    transition: 0.5s;
    }
    section.active{
    background:rgb(244, 56, 56);
    }
    section .container{
    position: relative;
    width: 808px;
    height: 500px;
    background:#fff;
    box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
    overflow:hidden;
    }
    section .container .user{
    position : absolute;
    top: 0;
    Left: 0;
    width: 100%;
    height: 100%;
    dispLay: flex;
    }
    section .container .user .imgBx{
    position : relative;
    width: 50%;
    height: 100%;
    transition: 0.5;
    }
    section .container .user .imgBx img{
    position: absolute;
    top: 0;
    Left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover
    }
    section .container .user .formBx{
    position: relative;
    width: 50%;
    height: 100%;
    background:#fff;
    display: flex;
    justify-content : center;
    align-items : center;
    padding : 40px;
    transition: 0.5s;
    }
    section .container .user .formBx h2{
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    width: 100%;
    margin-bottom: 10px;
    color: #555;
    }
    section .container .user .formBx input{
    width: 100%;
    padding: 1epx;
    background: #f5f5f5;
    color:#333;
    border:none;
    outline:none;
    box-shadow:none;
    font-size:14px;
    margin:8px 0;
    letter-spacing: 1px;
    font-weight: 300;
    }
    section .container .user .formBx input[type="submit"]{
    max-width: 100px;
    background: #677eff;
    color: #fff;
    cursor: pointer;
    font-size : 14px;
    font-weight: 500;
    Letter-spacing : 1px;
    transition: 1.5s;
    }
    section .container .user .signup .formBx input[type ="submit"]{
    background:red;
    }
    section .container .user .formBx .signup{
    position: relative;
    margin-top: 20px;
    font-size: 12px;
    letter-spacing: 1px;
    color: #555;
    text-transform:uppercase;
    font-weight:300;
    }
    section .container .user .formBx .signup a{
    font-weight: 600;
    text-decoration:none;
    color:#577eff;
    }
    section .container .signupBx{
    pointer-events:none;
    }
    section .container.active .signupBx{
    pointer-events:initial;
    }
    section .container .signupBx .formBx{
    top:100%;
    }
    section .container.active .signupBx .formBx{
    top:0;
    }
    section .container .signupBx .imgBx{
    top:-100%;
    transition:0.5s;
    }
    section .container.active .signupBx .imgBx{
    top:0;
    }
    
    section .container .signinBx .formBx{
    top:0;
    }
    section .container.active .signinBx .formBx{
    top:100%;
    }
    
    section .container .signinBx .imgBx{
    top:0;
    transition:0.5s;
    }
    section .container.active .signinBx .imgBx{
    top:-100%;
    }
    section .container .user .imgBx{
      background: #577eff;
    }
    section .container.active .user .imgBx{
      background: #577eff;
    }
    section .container .signinBx .imgBx .sideimg{
      color: #fff;
      padding: 35%;
      font-size: 50px;
      justify-content: center;
      align-self: center;
      align-items: center;
    }
    @media(max-width:991px)
    {
    section .container{
    max-width:400px;
    }
    section .container .imgBx{
    display: none;
    }
    section .container .user .formBx{
    width:100%;
    }
  }